<template>
  <div class="scrollBox">
    <div class="mygradeinfo">
      <header-nav :title="msg"></header-nav>
    <!-- 等级规则介绍 -->
      <div class="header">
        <p class="rule-tip">掘金宝原始股累积越多，掘金宝等级越高，可兑换的</p>
        <p class="rule-tip">实物也就越多，完成阅读/签到/邀请好友等任务都可</p>
        <p class="rule-tip">以加速升级。</p>
        <p class="grade-tip">掘金宝等级最低1级，最高24级。具体等级划分如下：</p>
      </div>
      <div class="content" ref="content">
        <div class="gradebox">
          <div class="gradepiebox" :ref="grade === item.grade ? 'activeEl' : ''" :class="`gradepiebox${index + 1}`" v-for="(item, index) in graderule" :key="index">
            <p v-if="grade === item.grade" class="tiplogo">
              当前等级
            </p>
            <p class="currentlogo"></p>
            <p class="vcoinpersent">{{item.shownum}}</p>
            <div class="vcoinimg margintop" ref="element" style="position: relative">
              <div class="imgbox">
                <img :src="require(`../../assets/img/grade/${item.grade}.png`)" alt="">
              </div>
              <div class="current-grade" v-if="grade === item.grade" :style="{height: `${currentvcoin / item.vcoin * 100}% `}" :class="{fullgrade: currentvcoin >= item.vcoin}"></div>
            </div>
            <p class="gradenum margintop" :class="{checkedgrade: grade === item.grade}">LV{{item.grade}}</p>
          </div>
        </div>
      </div>
      <div class="currentBox">
        <p>当前掘金宝<span>{{currentvcoin}}个</span>，还差<span>{{needvcoin}}个</span>可升级至LV{{grade + 1}}</p>
      </div>
      <div class="relationship-tip">
        <p>掘金宝数量和等级关系图</p>
      </div>
    </div>
    <div class="footer">
      <span class="lookaward" @click="jumpawardpage">查看我的奖品</span>
      <span class="getworks" @click="jumptaskpage">做任务升级</span>
    </div>
  </div>
</template>
<script>
import HeaderNav from '../task/header-nav'
export default {
  components: { HeaderNav },
  data () {
    return {
      msg: '我的等级',
      currentvcoin: '',
      needvcoin: 0,
      grade: '',
      currentheight: '',
      currentwidth: '',
      calposition: '',
      graderule: [
        {grade: 1, vcoin: 100, shownum: 100}, {grade: 2, vcoin: 200, shownum: 200}, {grade: 3, vcoin: 400, shownum: 400}, {grade: 4, vcoin: 600, shownum: 600},
        {grade: 5, vcoin: 800, shownum: 800}, {grade: 6, vcoin: 1000, shownum: 1000}, {grade: 7, vcoin: 1500, shownum: 1500}, {grade: 8, vcoin: 2000, shownum: 2000},
        {grade: 9, vcoin: 3000, shownum: 3000}, {grade: 10, vcoin: 5000, shownum: 5000}, {grade: 11, vcoin: 8000, shownum: 8000}, {grade: 12, vcoin: 10000, shownum: '1w'}, {grade: 13, vcoin: 15000, shownum: '1.5w'},
        {grade: 14, vcoin: 20000, shownum: '2w'}, {grade: 15, vcoin: 30000, shownum: '3w'}, {grade: 16, vcoin: 50000, shownum: '5w'}, {grade: 17, vcoin: 80000, shownum: '8w'},
        {grade: 18, vcoin: 100000, shownum: '10w'}, {grade: 19, vcoin: 150000, shownum: '15w'}, {grade: 20, vcoin: 200000, shownum: '20w'},
        {grade: 21, vcoin: 300000, shownum: '30w'}, {grade: 22, vcoin: 500000, shownum: '50w'}, {grade: 23, vcoin: 800000, shownum: '80w'}, {grade: 24, vcoin: 1000000, shownum: '100w'}
      ]
    }
  },
  computed: {
  },
  methods: {
    getIndex () {
      this.$api.get('MY_INDEX', {}, res => {
        this.currentvcoin = Math.floor(res.data.vcoin)
        this.grade = Number(res.data.grade)
        this.graderule.forEach(item => {
          if (this.grade === item.grade) {
            console.log(item.grade)
            this.needvcoin = Number(item.vcoin - this.currentvcoin)
          }
        })
        this.$nextTick(() => {
          this.$refs.content.scrollLeft = this.$utils.getElLeftPotion(this.$refs.activeEl[0]) - 168
        })
      })
    },
    // 跳转奖励页面
    jumpawardpage () {
      this.$router.push({name: 'AwardPag'})
    },
    // 跳转做任务页面
    jumptaskpage () {
      this.$router.push({name: 'task'})
    }
  },
  created () {
    this.getIndex()
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  .mygradeinfo {
    padding-top:30px;
    box-sizing:border-box;
    .header {
      margin-left: 30px;
      margin-top: 75px;
      .rule-tip {
        font-size: 28px;
        color: #666666;
        line-height: 38px;
      }
      .grade-tip {
        font-size: 28px;
        color: #4A4A4A;
        margin-top:20px;
      }
    }
    .content {
      width:690px;
      height:520px;
      margin-left: 30px;
      background: #FFFFFF;
      box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;
      border-radius: 4px;
      margin-top:30px;
      overflow-x:auto;
      position: relative;
      .gradebox {
        width:2976px;
        position: absolute;
        bottom:15px;
        margin-left:65px;
        .gradepiebox {
          display: inline-block;
          width:73px;
          margin-right:51px;
          text-align: center;
          font-size: 24px;
          color: #000000;
          .margintop {
            margin-top: 15px;
          }
          .current-grade {
            background:#FFBA49;
            position: absolute;
            z-index: 99;
            width: 18px;
            border-radius: 10px 10px 0 0;
            left: 52%;
            transform: translateX(-50%);
            bottom:4px;
          }
          .fullgrade {
            border-radius: 100px 100px 0px 0px;
          }
          .gradenum {
            width: 66px;
            display: inline-block;
            height:33px;
            line-height: 33px;
            background:url("../../assets/img/grade/graylogo.png") no-repeat center center;
            background-size: 65px 33px;
            text-align: center;
            font-size: 22px;
            padding-left:8px;
            color:#fff;
          }
          .checkedgrade {
            background: url("../../assets/img/grade/lvlogo.png") no-repeat 0% center;
          }
          .imgbox {
            width: 20px;
            margin-left:28px;
            img {
              height: 100%;
              width: 100%;
            }
          }

          .calheightpersent {
            background: #FFBA49;
          }
          .tiplogo {
            width:103px;
            height:39.6px;
            text-align: center;
            line-height: 30px;
            background:url("../../assets/img/personal/currentip.png") no-repeat center center;
            background-size: 103px 39.6px;
            font-size: 22px;
            color:#fff;
            margin-left: -15px;
            margin-bottom: 20px;
          }
          .vcoinpersent {
            font-size: 24px;
            color: #000000;
          }
        }
      }
      &::-webkit-scrollbar {
        display: none;
      }
    }

    .currentBox {
      width: 690px;
      height:114px;
      line-height: 114px;
      font-size: 28px;
      background: #FFBA49;
      box-shadow: 4px 4px 14px 0 #EBEAEF;
      text-align: center;
      margin-left: 30px;
      p {
        span {
          color:rgb(255,65,18);
          line-height: 40px;
        }
      }
    }
    .relationship-tip {
      text-align: center;
      font-size: 28px;
      color: #666666;
      line-height: 40px;
      margin-top: 20px;
    }
  }
  .footer {
    width:750px;
    height:100px;
    border-top: 1px solid #eeeeee;
    position: fixed;
    bottom:0px;
    span {
      display: inline-block;
      width: 216px;
      height:68px;
      line-height: 68px;
      text-align: center;
      font-size: 30px;
      cursor: pointer;
      margin-top:16px;
    }
    .lookaward {
      margin-left:116px;
      border: 1px solid #F38D32;
      border-radius: 40px;
      color:#F38D32
    }
    .getworks {
      margin-left:80px;
      background: #F38D32;
      border-radius: 40px;
      color:#fff;
    }
  }
}
</style>



