<template>
    <div>

    </div>
</template>
<script>
import wx from 'weixin-js-sdk'
export default {
  props: ['shareParams'],
  data () {
    return {
      setdomain: ''
    }
  },
  methods: {
    setShare () {
      var url = location.href
      if (url.indexOf('#') > -1) {
        url = url.split('#')[0]
      }
      let data = {}
      if (this.shareParams.mid) {
        data.mid = this.shareParams.mid
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(url), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          this.$api.get('USE_DOMAIN', { type: 'temp' }, res => {
            let newdomain = res.data.domain
            let sig = res.data
            this.pageUrl = newdomain
            let contentEl = document.createElement('div')
            contentEl.innerHTML = this.shares.desc
            let content = contentEl.innerText.substr(0, 25)
            
            var shareContent = {
              title: this.shares.title,
              desc: content, // 分享描述
              link: this.pageUrl + `?inviteCode=${this.$utils.cache('inviteCode')}&path=${this.$route.path}`, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: this.shares.imgUrl // 分享图标
            }
            // 配置微信签名信息
            wx.config({
              debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
              appId: sig.appid, // 必填，公众号的唯一标识
              timestamp: sig.timestamp, // 必填，生成签名的时间戳
              nonceStr: sig.nonceStr, // 必填，生成签名的随机串
              signature: sig.signature, // 必填，签名
              jsApiList: this.$store.state.common.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
            })
            wx.ready(function () {
              wx.onMenuShareTimeline({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  data.type = 2
                }
              })
              wx.onMenuShareAppMessage({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  data.type = 1
                }
              })
              wx.onMenuShareQQ({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  data.type = 3
                }
              })
            })
            wx.error((res) => {
            })
          })
        }
      })
    }
  },
  computed: {
    inviteCode: function () {
      return this.$store.state.inviteCode
    }
  },
  mounted () {
    this.shares = this.shareParams ? JSON.parse(JSON.stringify(this.shareParams)) : {
      title: '上掘金宝，看赚钱资讯！',
      desc: '洞察价值资讯，掌握热点事件 — 精准推荐，个性阅读，就上掘金宝！',
      link: location,
      imgUrl: '../../assets/img/my_log'
    }
    this.setShare()
  }
}
</script>



